function rot13n(input, n) {
  return input.split('').map(function (i) {
    return i.charCodeAt(0);
  }).map(function (d) {
    if (d >= 65 && d <= 90) {
      var x = d + n;
      var r = x % 91;
      if (r >= 0 && r < 65) {
        return r + 65;
      } else return x;
    } else return d;
  }).map(function (d) {
    if (d >= 97 && d <= 122) {
      var x = d + n;
      var r = x % 123;
      if (r >= 0 && r < 97) {
        return r + 97;
      } else return x;
    } else return d;
  }).map(function (d) {
    return String.fromCharCode(d);
  }).join('');
}
function rotations(input) {
  return Array(26).fill(0).map(function (_, i) {
    return i + 1;
  }).map(function (i) {
    return {
      i: i,
      rot: rot13n(input, i)
    };
  });
}
module.exports = {
  rot13n: rot13n,
  rotations: rotations
};