function _left(input, length, pad) {
  var out = input.toString();
  while (out.length < length) {
    out = pad + out;
  }
  return out;
}
function _right(input, length, pad) {
  var out = input.toString();
  while (out.length < length) {
    out += pad;
  }
  return out;
}
var padnumber = {
  left: function left(input, length) {
    return _left(input, length, '0');
  },
  right: function right(input, length) {
    return _right(input, length, '0');
  }
};
var padstring = {
  left: function left(input, length) {
    return _left(input, length, ' ');
  },
  right: function right(input, length) {
    return _right(input, length, ' ');
  }
};
module.exports = {
  left: _left,
  right: _right,
  padnumber: padnumber,
  padstring: padstring
};