function rot13(input) {
  return input.split('').map(function (i) {
    return i.charCodeAt(0);
  }).map(function (d) {
    return d >= 65 && d <= 90 ? d + 13 > 90 ? d - 13 : d + 13 : d;
  }).map(function (d) {
    return d >= 97 && d <= 122 ? d + 13 > 122 ? d - 13 : d + 13 : d;
  }).map(function (d) {
    return String.fromCharCode(d);
  }).reduce(function (a, c) {
    return a + c;
  }, '');
}
module.exports = rot13;